'use client';

import { Option } from '@/lib/question-utils';
import { useMemo } from 'react';
import { shuffleArray } from '@/lib/question-utils';

interface MultipleChoiceQuestionProps {
  question: {
    options?: Option[];
  };
  selectedOptions: string[];
  onChange: (selection: string[]) => void;
  allowMultiple?: boolean;
  shuffleOptionsEnabled?: boolean;
  disabled?: boolean;
}

export function MultipleChoiceQuestion({ 
  question, 
  selectedOptions = [],
  onChange,
  allowMultiple = false,
  shuffleOptionsEnabled = false,
  disabled = false
}: MultipleChoiceQuestionProps) {
  // For single selection
  const handleRadioChange = (optionId: string) => {
    onChange([optionId]);
  };
  
  // For multiple selection
  const handleCheckboxChange = (optionId: string) => {
    if (allowMultiple) {
      const newSelection = selectedOptions.includes(optionId)
        ? selectedOptions.filter(id => id !== optionId)
        : [...selectedOptions, optionId];
        
      onChange(newSelection);
    } else {
      // Ensure single selection even with checkbox
      onChange([optionId]);
    }
  };
  
  // Process options - shuffle or sort by order
  const processedOptions = useMemo(() => {
    if (!question.options || question.options.length === 0) return [];
    
    if (shuffleOptionsEnabled) {
      // Use the shuffleArray function to randomize options
      return shuffleArray(question.options);
    } else {
      // Sort options by order as before
      return [...question.options].sort((a, b) => a.order - b.order);
    }
  }, [question.options, shuffleOptionsEnabled]);
  
  if (allowMultiple) {
    // Multiple selection version
    return (
      <div className="space-y-3">
        {processedOptions.map(option => (
          <div key={option.id} className="flex items-start space-x-2">
            <input 
              type="checkbox" 
              id={option.id} 
              name="answer"
              value={option.id}
              checked={selectedOptions.includes(option.id)}
              onChange={() => handleCheckboxChange(option.id)}
              className="mt-1"
              disabled={disabled}
            />
            <label 
              htmlFor={option.id} 
              className={`flex-1 cursor-pointer ${disabled ? 'cursor-not-allowed opacity-70' : ''}`}
              dangerouslySetInnerHTML={{ __html: option.content }}
            />
          </div>
        ))}
        <p className="text-sm text-muted-foreground mt-2">
          Select all that apply.
        </p>
      </div>
    );
  }
  
  // Single selection version
  return (
    <div className="space-y-3">
      {processedOptions.map(option => (
        <div key={option.id} className="flex items-start space-x-2">
          <input 
            type="radio" 
            id={option.id} 
            name="answer"
            value={option.id}
            checked={selectedOptions.length > 0 && selectedOptions[0] === option.id}
            onChange={() => handleRadioChange(option.id)}
            className="mt-1"
            disabled={disabled}
          />
          <label 
            htmlFor={option.id} 
            className={`flex-1 cursor-pointer ${disabled ? 'cursor-not-allowed opacity-70' : ''}`}
            dangerouslySetInnerHTML={{ __html: option.content }}
          />
        </div>
      ))}
    </div>
  );
} 